import React from "react";
import { Link } from "react-router-dom";

const Nav = ()=> {
    return (
      <div>
        <nav className="navbar navbar-expand-lg bg-dark navbar-dark fixed-top">
          <div className="container-fluid">
            <Link className="navbar-brand" to="/">
              NewsMonkey
            </Link>
            <button
              className="navbar-toggler"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#navbarSupportedContent"
              aria-controls="navbarSupportedContent"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon"></span>
            </button>
            <div
              className="collapse navbar-collapse"
              id="navbarSupportedContent"
            >
              <ul className="navbar-nav me-auto mb-2 mb-lg-0" style={{fontSize : "20px"}}>
                <li className="nav-item"><Link className="nav-link" aria-current="page" to="/">Home</Link></li>
                {/* <li className="nav-item"><Link className="nav-link" to="/about">About</Link></li> */}
                <li className="nav-item"><Link className="nav-link" to="/health">Health </Link></li>
                <li className="nav-item"><Link className="nav-link" to="/science">Science </Link></li>
                <li className="nav-item"> <Link className="nav-link" to="/sports">Sports </Link></li>
                <li className="nav-item"><Link className="nav-link" to="/bussiness">Bussiness</Link></li>
                <li className="nav-item"><Link className="nav-link" to="/entertaiment">Entertaiment </Link></li>
                <li className="nav-item"><Link className="nav-link" to="/general">General </Link></li>
                <li className="nav-item"><Link className="nav-link" to="/technology">Technology</Link></li>
              </ul>
            </div>
          </div>
        </nav>
      </div>
    );
}

export default Nav;
